@import url(https://fonts.googleapis.com/css?family=Newsreader);
:root {
    font-size: 20px;
    overflow-x: hidden;
}

html, body {
    min-height: 100%;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* Set header+main combo to be a certain amount minus footer, with overflow visible  */
.top-content {
    min-height: calc(100vh - 76px); /* footer height is currently 36px + 20px top & bottom margins*/
    overflow: visible;
}

.nav-item span:hover {
    font-weight: 700;
}

h1 {
    font-family: "Times New Roman", Times, serif;
}

.my-img {
    /* max-height: 386px; */
    max-height: 53.7vh;
    width: auto;
}

.project-img {
    max-width: 270px;
    height: auto;
}

svg {
    fill: currentColor;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes fade-in {
    from { 
        -webkit-transform: translateY(2em); 
                transform: translateY(2em);
        opacity: 0;
    }
    to {
        -webkit-transform: translateY(0em);
                transform: translateY(0em);
        opacity: 1;
    }
}

@keyframes fade-in {
    from { 
        -webkit-transform: translateY(2em); 
                transform: translateY(2em);
        opacity: 0;
    }
    to {
        -webkit-transform: translateY(0em);
                transform: translateY(0em);
        opacity: 1;
    }
}

.load-anim-0 {
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

.load-anim-1 {
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

h1, h2, h3, h4, h5, h6 {
    font-family: "Newsreader", sans-serif;
}

ul {
    list-style-type: none;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

.skill-header {
    text-align: center;
}

.skill-list {
    border: 1px solid gray;
    border-radius: 1rem;
    margin: 1rem;
    padding: 0.5rem 2rem;
    text-align: center;
    font-weight: bold;
    color: rgb(223, 215, 215);
    background-image: linear-gradient(to right bottom, rgb(165, 168, 198), rgba(0, 0, 0, 0.5));
}

